/*  
   Profile Page Styling - Matches Leaderboard
     */

/*  
   Profile Page Container
     */

   .profile-container {
    max-width: 420px;
    width: 380px; /* Set a fixed width for consistency */
    margin: 40px auto;
    padding: 30px;
    background-color: #d5c5f1;
    border-radius: 20px;
    box-shadow: 0px 8px 16px rgba(0, 0, 0, 0.2);
    font-family: 'Poppins', sans-serif;
    position: relative;
    min-height: 640px; /* Set a minimum height to ensure containers are the same size */
    display: flex;
    flex-direction: column;
  }
  
  .edit-profile-form {
    width: 100%;
    margin-top: 10px;
    /* Make form take up appropriate space within container */
    flex: 1;
    display: flex;
    flex-direction: column;
  }
  
  /*  
     Profile Header (Image + Name)
       */
  
  .profile-header {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 20px;
  }
  
  /* Profile Image */
  .profile-image {
    width: 100px; /*   Consistent profile image size */
    height: 100px;
    border-radius: 50%; /*   Circular shape */
    border: 3px solid #4c3b6d; /*   Purple outline matches theme */
    object-fit: cover; /*   Ensures image maintains aspect ratio */
    box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.2);
    margin-bottom: 10px;
    transition: all 0.3s ease;
  }
  
  /*  
     User Information Styling
       */
  
  .profile-details p {
    font-size: 14px;
    font-weight: bold;
    color: #4c3b6d; /*   Dark purple for contrast */
    margin: 5px 0;
  }
  
  /*  
     Edit Profile Button
       */
  
  .edit-profile-btn {
    background-color: #7548d4; /*   Matches leaderboard button styling */
    color: white;
    border: none;
    padding: 10px 15px;
    font-size: 16px;
    font-weight: bold;
    border-radius: 10px;
    cursor: pointer;
    transition: background-color 0.2s ease-in-out, transform 0.1s ease-in-out;
    margin-right: 10px;
  }
  
  .edit-profile-btn:hover {
    background-color: #5b36a6; /*   Darker purple on hover */
    transform: scale(1.05);
  }
  
  .edit-profile-btn:active {
    transform: scale(0.95);
  }

  /*  
   Delete Profile Button
     */

  .delete-profile-btn {
    background-color: #d44848; /* Red background for delete action */
    color: white;
    border: none;
    padding: 10px 15px;
    font-size: 16px;
    font-weight: bold;
    border-radius: 10px;
    cursor: pointer;
    transition: background-color 0.2s ease-in-out, transform 0.1s ease-in-out;
    margin-top: 10px;
  }

  .delete-profile-btn:hover {
    background-color: #b53030; /* Darker red on hover */
    transform: scale(1.05);
  }

  .delete-profile-btn:active {
    transform: scale(0.95);
  }

  /*  
    Delete Confirmation Dialog
      */

  .delete-confirmation-overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.7);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1000;
  }

  .delete-confirmation-dialog {
    background-color: white;
    border-radius: 15px;
    padding: 20px;
    width: 90%;
    max-width: 350px;
    text-align: center;
    box-shadow: 0px 10px 25px rgba(0, 0, 0, 0.3);
    animation: fadeIn 0.3s ease-out;
  }

  @keyframes fadeIn {
    from { opacity: 0; transform: translateY(-20px); }
    to { opacity: 1; transform: translateY(0); }
  }

  .delete-confirmation-dialog h3 {
    color: #d44848;
    margin-top: 0;
  }

  .delete-confirmation-dialog p {
    color: #555;
    margin-bottom: 20px;
  }

  .delete-confirmation-buttons {
    display: flex;
    flex-direction: column;
    gap: 10px;
  }

  .delete-confirm-btn {
    background-color: #d44848;
    color: white;
    border: none;
    padding: 12px;
    border-radius: 8px;
    font-weight: bold;
    cursor: pointer;
    transition: background-color 0.2s;
  }

  .delete-confirm-btn:hover {
    background-color: #b53030;
  }

  .delete-cancel-btn {
    background-color: #6c757d;
    color: white;
    border: none;
    padding: 12px;
    border-radius: 8px;
    font-weight: bold;
    cursor: pointer;
    transition: background-color 0.2s;
  }

  .delete-cancel-btn:hover {
    background-color: #5a6268;
  }
  
  /*  
     Completed Tasks Section
       */
  
  .completed-tasks {
    margin-top: 20px;
  }
  
  /* Completed Tasks Header */
  .completed-tasks h3 {
    font-size: 18px;
    font-weight: bold;
    color: #4c3b6d; /*   Matches leaderboard title */
    margin-bottom: 10px;
  }
  
  /* Completed Tasks List */
  .completed-tasks ul {
    list-style-type: none; /*   Removes bullet points */
    padding: 0;
  }
  
  /* Completed Task Items */
  .completed-tasks li {
    background-color: #b3e6c9; /*   Soft green to indicate task completion */
    color: #2e604a;
    font-weight: bold;
    padding: 10px;
    margin: 5px 0;
    border-radius: 10px;
  }
  
  /*  
     Navigation Buttons
       */
  
  .buttons {
    display: flex;
    flex-direction: column; /*   Stack buttons vertically */
    gap: 10px;
    margin-top: 20px;
  }
  
  /* Buttons Styling */
  .buttons button {
    background-color: #4c3b6d; /*   Matches leaderboard button color */
    color: white;
    border: none;
    padding: 12px;
    font-size: 16px;
    font-weight: bold;
    border-radius: 10px;
    cursor: pointer;
    transition: background-color 0.2s ease-in-out, transform 0.1s ease-in-out;
  }
  
  /* Button Hover Effect */
  .buttons button:hover {
    background-color: #3a2b5e; /*   Darker purple on hover */
    transform: scale(1.05);
  }
  
  /* Button Click Effect */
  .buttons button:active {
    transform: scale(0.95);
  }
  
  /* Edit profile form */
  
  .edit-profile-form {
    width: 100%;
    margin-top: 10px;
    flex: 1;
    display: flex;
    flex-direction: column;
    gap: 15px; /* Add space between form elements */
  }
  
  .edit-profile-form button {
    background-color: #7548d4;
    color: white;
    border: none;
    padding: 10px;
    border-radius: 8px;
    cursor: pointer;
    font-weight: bold;
    margin-top: 5px;
  }
  
  .edit-profile-form button[type="button"] {
    background-color: #9275d1;
  }
  
  /*  
     Mobile Responsive Styling
       */
  
  @media (max-width: 500px) {
    .profile-container {
      width: 90%; /*   Makes container responsive */
      padding: 15px;
    }
  
    .profile-image {
      width: 90px;
      height: 90px;
    }
  
    .badges-grid {
      grid-template-columns: 1fr;
    }
  
    .hint-item {
      font-size: 11px;
    }
  
    .completed-tasks li {
      font-size: 14px; /*   Adjust font size for readability */
    }
  }
  